// 转换 道具 属性

// 转换 道具 attr 属性 添加 attr[].nameText
export const transItemText = (attr) => {
    let attrText = [];

    for (let i = 0; i < attr.length; ++i) {
        let obj = attr[i];
        switch (attr[i].name) {
            case "waigong":
                obj.nameText = "外攻";
                break;
            case "neigong":
                obj.nameText = "内攻";
                break;
            case "waifang":
                obj.nameText = "外防";
                break;
            case "neifang":
                obj.nameText = "内防";
                break;
            case "huixin":
                obj.nameText = "会心";
                break;
            case "huishang":
                obj.nameText = "会伤";
                break;
            case "renxing":
                obj.nameText = "韧性";
                break;
        }
        attrText.push(obj);
    }

    return attrText;
};

export const transItemType = (type) => {
    let typeList = {
        0: "道具",
        1: "任务",
        2: "武器",
        3: "防具",
        4: "饰品",
    };

    return typeList[type];
};
